html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Inter", sans-serif;
  font-size: 1.5rem;
  line-height: 1.8; }

p {
  color: black;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

a {
  text-decoration: none; }

img,
picture {
  width: 100%;
  display: block; }

h1,
h2,
h3 {
  font-family: "Inter", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.contenido-centrado {
  max-width: 800px; }

@media (min-width: 768px) {
  .alinear-derecha {
    display: flex;
    justify-content: flex-end; } }

.w-100 {
  width: 100%; }

/* UI */
html {
  background-color: #090b1a; }
  @media (min-width: 768px) {
    html {
      display: flex;
      height: 100vh;
      justify-content: center;
      align-items: center; } }

.contenedor {
  display: flex;
  flex-direction: column-reverse; }
  @media (min-width: 768px) {
    .contenedor {
      display: grid;
      gap: 0;
      grid-template-columns: repeat(2, 1fr); } }
  .contenedor .img-filter {
    width: 100%;
    height: 100%;
    opacity: 0.5;
    position: absolute;
    background-color: #aa5cdb;
    background-size: cover;
    background-position: center center;
    z-index: 1; }
  .contenedor .img {
    height: 100%;
    position: relative; }
    .contenedor .img img {
      height: 100%; }

.conetendor__text {
  padding: 5rem 5rem 0 5rem;
  background-color: #1b1938; }
  .conetendor__text h3 {
    text-align: left;
    color: white;
    font-weight: 700; }
    .conetendor__text h3 span {
      color: #aa5cdb; }
  .conetendor__text p {
    margin-right: 0;
    text-align: left;
    color: white; }

@media (min-width: 768px) {
  .text__follow {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    gap: 2rem; } }

.text__follow .text {
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (min-width: 768px) {
    .text__follow .text {
      flex-direction: column;
      text-align: center;
      font-weight: 700; } }

.text__follow span {
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: 300; }

.text__info {
  max-width: 85%;
  font-size: 1.8rem;
  font-weight: 300; }
